<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="注册赚钱 - 奖励未到账"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content qk-complain-hitask">
      <div class="top-notice">为了处理更高效，请确保以下信息的真实准确。</div>

      <list class="list-android">
        <hl-item class="text-input">
          <div class="sub-title">姓名</div>
          <!--<von-input type="text" v-model="info.username" placeholder="绑卡使用的姓名"></von-input>-->
          <input type="text" v-model="info.username" placeholder="绑卡使用的姓名">
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">日期</div>
          <div class="von-input-wrapper">
            <label class="item-input thin-border von-input">
              <input @change="dateSelect" v-model="taskStartDate" type="date" required aria-required="true"
                     data-placeholder="领取任务的日期"/>
            </label>
          </div>
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">名称</div>
          <!--<div style="position:absolute; top: 37px; font-size: ">选择待申诉任务</div>-->
          <select v-model="info.task_id" :class="{'select-color': option_selected}">
            <option v-for="option in tasks"
                    v-bind:selected="option.selected"
                    v-bind:disabled="option.disabled"
                    v-bind:value="option.id">
              {{ option.title }}
            </option>
          </select>
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">手机号</div>
          <!--<von-input type="text" v-model="info.mobile_number" placeholder="绑卡使用的手机号"></von-input>-->
          <input type="tel" v-model="info.mobile_number" placeholder="绑卡使用的手机号">
        </hl-item>
        <hl-item class="hl-item-borderless-bottom text-upload">
          <div class="title">上传截图</div>
          <div class="item-note">
            <div class="note-require">请上传能证明任务完成的截图（如：带个人信息的用户界面）</div>
            <div class="note-hint">信用卡开卡任务申诉请上传已收到信用卡的正面照片和开卡成功的短信（如有）截图。</div>
          </div>
          <div class="upload">
            <div class="image-area">
              <div v-for="image in imageList" class="preview">
                <img :src="image">
              </div>
              <div class="upload-btn">
                <input type="file" @change="doUpload($event.target)">
              </div>
            </div>
          </div>
        </hl-item>
      </list>

      <div style="padding: 10px 15px 15px 15px;">
        <button :class="{'button-active' : !buttonDisabled}" class="button button-block button-assertive" @click="submit()">
          提交
        </button>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .qk-complain-hitask {
    margin-top: 50px;
    background-color: #fff !important;

    @include font-regular()

    .list-android {
      padding: 0 20px;
    }

    .top-notice {
      height: 38px;
      padding: 20px 20px 0;
      font-size: 13px;
      color: #B5B5B5;
      letter-spacing: 0.11px;
    }

    .item {
      padding: 0 !important;
      margin: 0;
      font-size: 14px;
    }

    .text-id {
      color: #484746;
      line-height: 50px;
    }

    .text-input {
      height: 74px;

      .sub-title {
        height: 17px;
        line-height: 17px;
        font-size: 12px;
        color: #B5B5B5;
        margin-top: 20px;
      }

      .von-input {
        width: 200px;
        color: #B5B5B5;
        height: 37px;
        min-height: 37px;
        margin: 0;
        padding: 0;
      }

      .von-input::after {
        border: 0;
      }

      input[type='date'] {
        -webkit-appearance: none;
      }

      input[type="date"]::before {
        content: attr(data-placeholder);
        width: 100%;
        color: #DDDDDD;
      }

      input[type="date"]:focus::before,
      input[type="date"]:valid::before {
        display: none;
      }

      input {
        padding: 17px 0;
        color: #3a3a3a;
        height: 37px;
        line-height: 36px;
        padding: 0;
        width: 200px;
        border-bottom: 1px solid #DDDDDD;
        font-size: 24px;
        border-radius: 0;

        @include font-regular();

        &::-webkit-input-placeholder {
          height: 36px;
          line-height: 36px;
          color: #DDDDDD;
        }
      }

      select {
        -webkit-appearance: none;
        padding: 0;
        border: 0;
        color: #DDDDDD;
        background-color: #FFFFFF;
        height: 37px;
        line-height: 37px;
        width: 200px;
        font-size: 24px;
        border-bottom: 1px solid #DDDDDD;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;

        &.select-color {
          color: #3a3a3a;
        }
      }
    }

    .text-upload {
      padding-top: 35px !important;
      color: #666666;

      .title {
        height: 22px;
        line-height: 22px;
        font-size: 16px;
        color: #3A3A3A;
      }

      .item-note {
        float: initial;
        font-size: 13px;
        color: #888888;
        letter-spacing: 1px;
        line-height: 22px;

        .note-require {
          height: 44px;
          margin: 12px 0 10px;
          white-space: pre-wrap;
          line-height: 1.69;
        }

        .note-hint {
          margin-bottom: 20px;
          white-space: pre-wrap;
        }
      }

      .upload {
        float: left;
        width: 100%;
        height: 50px;

        .image-area {
          float: left;

          .preview {
            width: 50px;
            height: 50px;
            margin-right: 10px;
            float: left;

            img {
              width: 50px;
              height: 50px;
              padding: 0;
              margin: 0;
            }
          }

          .upload-btn {
            float: left;
            width: 50px;
            height: 50px;
            background: url("//assets.qkcdn.com/images/2bfa8934bf559c1550f052e282b06924.png") no-repeat top center;
            background-size: 100% 100%;

            input {
              width: 50px;
              height: 50px;
              opacity: 0;
            }
          }
        }

        .showcase {
          text-align: right;
          float: right;
          line-height: 50px;

          div {
            float: left;

            @include font-regular();

            font-size: 13px;
            color: #3A3A3A;

            &.last {
              margin-left: 19px;
            }
          }

          div::after {
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 0;
            height: 0;
            border-top: 3px solid transparent;
            border-bottom: 3px solid transparent;
            border-left: 4px solid #3A3A3A;
            margin-left: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }

    .button {
      border-radius: 2px;
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      margin-top: 0;
      background: #DDDDDD;

      @include font-semibold();
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #EF4F2A;
    }

    .hairline-bottom::after,
    .hairline-top::before {
      visibility: hidden;
    }
  }

  .von-input-wrapper .input-clear {
    display: none !important;
  }

</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import {mapActions} from 'vuex'

  export default {
    data () {
      return {
        info: {
          task_id: '',
          username: '',
          mobile_number: '',
          image: ''
        },
        taskStartDate: '',
        tasks: [
          {id: '', title: '选择待申诉任务', selected: true, disabled: true}
        ],
        imageList: [],
        option_selected: false
      }
    },
    components: {
      AnimateHeader
    },

    watch: {
      'info.task_id': function (v) {
        if (v) {
          this.option_selected = true
        } else {
          this.option_selected = false
        }
      }
    },

    computed: {
      buttonDisabled () {
        let info = this.info
        for (let key in info) {
          if (info[key] === '') {
            return true
          }
        }
        return false
      }
    },

    mounted () {
      if (this.$route.query.taskId) {
        this.info.task_id = this.$route.query.taskId
        this.info.taskStartDate = this.$route.query.taskDate
        this.tasks = [{id: this.taskId, title: this.$route.query.taskName}]
      }
    },

    methods: {
      ...mapActions('help', [
        'submitRegisterTaskInfo',
        'getTaskByDate',
        'upload'
      ]),

      dateSelect () {
        this.getTaskByDate(this.taskStartDate).then((tasks) => {
          let data = [{
            id: '',
            disabled: true,
            selected: true,
            title: '选择待申诉任务'
          }]
          if (tasks.length) {
            for (var v of tasks) {
              v.disabled = false
              v.selected = false
              data.push(v)
            }

            this.tasks = data
            this.info.task_id = this.tasks[1].id
          } else {
            this.tasks = data
            this.info.task_id = this.tasks[0].id
            $toast.show('选择的日期上并没有开始任务')
          }
        })
      },

      submit () {
        if (!this.taskStartDate) {
          $toast.show('没有选择 领取任务的日期！')
          return
        }

        if (!this.info.task_id) {
          $toast.show('没有选择 待申诉任务！')
          return
        }

        if (!this.info.username.trim()) {
          $toast.show('没有填写 绑卡使用的姓名！')
          return
        }

        if (!this.info.mobile_number) {
          $toast.show('没有填写 绑卡使用的手机号码！')
          return
        }
        if (!/^1[3456789]\d{9}$/.test(this.info.mobile_number)) {
          $toast.show('请输入正确的手机号！')
          return
        }
//        let imageUrl = ''
//        for (var index in this.imageList) {
//          let image = this.imageList[index]
//          if (index === this.imageList.length - 1) {
//            imageUrl += image
//          } else {
//            imageUrl += image + ';'
//          }
//        }
        if (this.info.image === '') {
          $toast.show('没有上传截图！')
          return
        }
        this.submitRegisterTaskInfo(this.info)
          .then(() => {
            $toast.show('提交成功')
            $router.back('/help')
          })
      },

      doUpload (file) {
        if (this.imageList.length === 4) {
          $toast.show('最多上传4张图片')
          return
        }

        this.upload(file).then((payload) => {
          let uploadData = payload.files['0']
          let file = uploadData.hash + '.' + uploadData.ext
          let uploadUrl = payload.uri_prefix + '/' + file
          this.imageList.push(uploadUrl)
          let imageUrl = ''
          for (var index in this.imageList) {
            let image = this.imageList[index]
            if (index === this.imageList.length - 1) {
              imageUrl += image
            } else {
              imageUrl += image + ';'
            }
          }
          this.info.image = imageUrl
          $toast.show('上传成功')
        }, (err) => {
          $toast.show(err)
        })
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/help/self')
        $router.back({path})
      }
    }
  }
</script>



